// first it will take us to the spotify authentication 
export const authEndpoint = "https://accounts.spotify.com/authorize";

// after completing the authentication it redirect to our local app
const redirectUri = "http://localhost:3000/";

const clientId = "8be8aba21ea04ef69b7278d2324ef95f";

const scopes = [
    "user-read-currently-playing",
    "user-read-recently-played",
    "user-read-playback-state",
    "user-top-read",
    "user-modify-playback-state",
];

export const getTokenFromUrl = () => {
    // to get the token
    return window.location.hash
    .substring(1)
    .split('&') // splits from the &
    .reduce((intitial, item) => {
        // #accessToken=myKey&name=pal
        let parts = item.split('=');
        intitial[parts[0]] = decodeURIComponent(parts[1]);
        return intitial;
    },{});
}
export const loginUrl = `${authEndpoint}?client_id=${clientId}&redirect_uri=${redirectUri}&scope=${scopes.join("%20")}&response_type=token&show_dialog=true`;

